'use client';

import { useState } from 'react';
import {
  ScanLine,
  Package,
  Box,
  Truck,
  Check,
  AlertTriangle,
  Plus,
  Minus,
  ChevronRight,
  Printer,
  Tag,
  Scale,
  Ruler,
  Split,
  X,
} from 'lucide-react';
import {
  orders,
  shippingBoxes,
  getProductById,
  getBoxById,
  suggestBox,
  formatCurrency,
} from '@/data/mockData';
import { Order, ShippingBox, Shipment, ShipmentItem } from '@/types';

// Mock shipping rates
const mockShippingRates = [
  { carrier: 'USPS', service: 'Priority Mail', price: 8.95, days: '2-3' },
  { carrier: 'USPS', service: 'Ground Advantage', price: 5.45, days: '3-5' },
  { carrier: 'UPS', service: 'Ground', price: 9.25, days: '3-5' },
  { carrier: 'UPS', service: '2-Day Air', price: 18.75, days: '2' },
  { carrier: 'FedEx', service: 'Ground', price: 8.95, days: '3-5' },
  { carrier: 'FedEx', service: 'Express', price: 22.50, days: '1-2' },
];

export default function PackPage() {
  const [toteBarcode, setToteBarcode] = useState('');
  const [selectedOrder, setSelectedOrder] = useState<Order | null>(null);
  const [selectedBoxId, setSelectedBoxId] = useState<string | null>(null);
  const [selectedRate, setSelectedRate] = useState<number | null>(null);
  const [showSplitModal, setShowSplitModal] = useState(false);
  const [splitShipments, setSplitShipments] = useState<Shipment[]>([]);
  const [currentShipmentIndex, setCurrentShipmentIndex] = useState(0);

  // Get orders ready to pack
  const ordersToPack = orders.filter((o) => o.status === 'to_pack');

  const handleScanTote = () => {
    // In real app, would lookup order by tote barcode
    const order = ordersToPack[0];
    if (order) {
      setSelectedOrder(order);
      // Auto-suggest box
      if (order.suggestedBoxId) {
        setSelectedBoxId(order.suggestedBoxId);
      }
      // Initialize single shipment
      setSplitShipments([
        {
          id: `ship-new-1`,
          orderId: order.id,
          shipmentNumber: 1,
          boxId: order.suggestedBoxId,
          items: order.items.map((item) => ({
            orderItemId: item.id,
            productId: item.productId,
            bundleId: item.bundleId,
            quantity: item.quantity,
          })),
          status: 'packing',
          gripperStickersUsed: order.gripperStickersNeeded,
        },
      ]);
    }
  };

  const handleBoxSelect = (boxId: string) => {
    setSelectedBoxId(boxId);
    if (splitShipments.length > 0) {
      const updated = [...splitShipments];
      updated[currentShipmentIndex].boxId = boxId;
      setSplitShipments(updated);
    }
  };

  const handleAddShipment = () => {
    if (!selectedOrder) return;

    const newShipment: Shipment = {
      id: `ship-new-${splitShipments.length + 1}`,
      orderId: selectedOrder.id,
      shipmentNumber: splitShipments.length + 1,
      items: [],
      status: 'packing',
      gripperStickersUsed: 0,
    };
    setSplitShipments([...splitShipments, newShipment]);
    setCurrentShipmentIndex(splitShipments.length);
  };

  const handleMoveItem = (
    orderItemId: string,
    fromShipmentIndex: number,
    toShipmentIndex: number,
    quantity: number
  ) => {
    const updated = [...splitShipments];

    // Find item in source shipment
    const fromShipment = updated[fromShipmentIndex];
    const itemIndex = fromShipment.items.findIndex(
      (i) => i.orderItemId === orderItemId
    );

    if (itemIndex === -1) return;

    const item = fromShipment.items[itemIndex];

    // Reduce quantity in source
    if (item.quantity <= quantity) {
      fromShipment.items.splice(itemIndex, 1);
    } else {
      item.quantity -= quantity;
    }

    // Add to destination
    const toShipment = updated[toShipmentIndex];
    const existingItem = toShipment.items.find(
      (i) => i.orderItemId === orderItemId
    );
    if (existingItem) {
      existingItem.quantity += quantity;
    } else {
      toShipment.items.push({
        orderItemId,
        productId: item.productId,
        bundleId: item.bundleId,
        quantity,
      });
    }

    setSplitShipments(updated);
  };

  const handlePrintLabel = () => {
    if (!selectedRate) {
      alert('Please select a shipping rate first');
      return;
    }
    alert('Label printed! (Mock)');
  };

  const handleMarkShipped = () => {
    alert('Order marked as shipped! (Mock)');
    setSelectedOrder(null);
    setSelectedBoxId(null);
    setSelectedRate(null);
    setSplitShipments([]);
    setToteBarcode('');
  };

  const currentShipment = splitShipments[currentShipmentIndex];
  const suggestedBox = selectedOrder
    ? suggestBox(selectedOrder.totalVolume || 0, selectedOrder.totalWeight || 0)
    : null;

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div>
        <h1 className="text-2xl font-bold text-white">Pack Station</h1>
        <p className="text-slate-400">
          {ordersToPack.length} orders ready to pack
        </p>
      </div>

      {/* Tote Scanner */}
      <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-6">
        <div className="flex items-center gap-4">
          <div className="flex h-12 w-12 items-center justify-center rounded-xl bg-purple-500/20">
            <ScanLine className="h-6 w-6 text-purple-400" />
          </div>
          <div className="flex-1">
            <input
              type="text"
              value={toteBarcode}
              onChange={(e) => setToteBarcode(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && handleScanTote()}
              placeholder="Scan tote barcode or enter order number..."
              className="w-full rounded-lg bg-slate-700/50 border border-slate-600/50 py-3 px-4 text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-purple-500/50"
            />
          </div>
          <button
            onClick={handleScanTote}
            className="rounded-lg bg-purple-500 px-6 py-3 text-sm font-medium text-white hover:bg-purple-600 transition-colors"
          >
            Load Order
          </button>
        </div>
      </div>

      {selectedOrder && (
        <div className="grid grid-cols-1 gap-6 lg:grid-cols-3">
          {/* Order Details */}
          <div className="lg:col-span-2 space-y-6">
            {/* Order Info */}
            <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-6">
              <div className="flex items-center justify-between mb-4">
                <div>
                  <h2 className="text-lg font-semibold text-white">
                    Order #{selectedOrder.orderNumber}
                  </h2>
                  <p className="text-sm text-slate-400 flex items-center gap-1">
                    {selectedOrder.customer.name} 
                    <span className={`inline-flex items-center gap-1 ${
                      selectedOrder.channel === 'shopify' ? 'text-green-400' : 'text-orange-400'
                    }`}>
                      <i className={selectedOrder.channel === 'shopify' ? 'fab fa-shopify' : 'fab fa-amazon'}></i>
                      {selectedOrder.channel === 'shopify' ? 'Shopify' : 'Amazon'}
                    </span>
                  </p>
                </div>
                <div className="flex items-center gap-2">
                  <button
                    onClick={() => setShowSplitModal(true)}
                    className="flex items-center gap-2 rounded-lg bg-slate-700 px-3 py-2 text-sm font-medium text-white hover:bg-slate-600 transition-colors"
                  >
                    <Split className="h-4 w-4" />
                    Split Shipment
                  </button>
                </div>
              </div>

              {/* Shipment Tabs */}
              {splitShipments.length > 1 && (
                <div className="flex gap-2 mb-4 border-b border-slate-700/50 pb-4">
                  {splitShipments.map((shipment, index) => (
                    <button
                      key={shipment.id}
                      onClick={() => setCurrentShipmentIndex(index)}
                      className={`flex items-center gap-2 rounded-lg px-3 py-2 text-sm font-medium transition-colors ${
                        currentShipmentIndex === index
                          ? 'bg-emerald-500/20 text-emerald-400'
                          : 'text-slate-400 hover:bg-slate-700/50'
                      }`}
                    >
                      <Box className="h-4 w-4" />
                      Shipment {index + 1}
                      <span className="text-xs">
                        ({shipment.items.reduce((sum, i) => sum + i.quantity, 0)}{' '}
                        items)
                      </span>
                    </button>
                  ))}
                  <button
                    onClick={handleAddShipment}
                    className="flex items-center gap-1 rounded-lg px-3 py-2 text-sm text-slate-400 hover:text-white hover:bg-slate-700/50 transition-colors"
                  >
                    <Plus className="h-4 w-4" />
                    Add
                  </button>
                </div>
              )}

              {/* Order Items */}
              <div className="space-y-3">
                {selectedOrder.items.map((item) => {
                  const product = item.productId
                    ? getProductById(item.productId)
                    : null;
                  const inCurrentShipment = currentShipment?.items.find(
                    (i) => i.orderItemId === item.id
                  );
                  const qtyInShipment = inCurrentShipment?.quantity || 0;

                  return (
                    <div
                      key={item.id}
                      className={`flex items-center justify-between rounded-lg p-4 ${
                        qtyInShipment > 0
                          ? 'bg-emerald-500/10 border border-emerald-500/30'
                          : 'bg-slate-700/30 border border-slate-600/30'
                      }`}
                    >
                      <div className="flex items-center gap-4">
                        <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-slate-700/50">
                          <Package className="h-5 w-5 text-slate-400" />
                        </div>
                        <div>
                          <p className="text-sm font-medium text-white">
                            {item.name}
                          </p>
                          <p className="text-xs text-slate-400">
                            SKU: {item.sku}
                            {product?.dimensions && (
                              <span className="ml-2">
                                 {product.dimensions.length}
                                {product.dimensions.width}
                                {product.dimensions.height}&quot;
                              </span>
                            )}
                          </p>
                        </div>
                      </div>
                      <div className="flex items-center gap-4">
                        <div className="text-right">
                          <p className="text-sm font-medium text-white">
                            {qtyInShipment} / {item.quantity}
                          </p>
                          <p className="text-xs text-slate-400">in shipment</p>
                        </div>
                        {qtyInShipment === item.quantity && (
                          <div className="flex h-8 w-8 items-center justify-center rounded-full bg-emerald-500/20">
                            <Check className="h-4 w-4 text-emerald-400" />
                          </div>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>

              {/* Gripper Stickers Info */}
              <div className="mt-4 rounded-lg bg-amber-500/10 border border-amber-500/30 p-3">
                <div className="flex items-center gap-2">
                  <Tag className="h-4 w-4 text-amber-400" />
                  <span className="text-sm text-amber-300">
                    {selectedOrder.gripperStickersNeeded} gripper stickers will be
                    auto-deducted on ship
                  </span>
                </div>
              </div>
            </div>

            {/* Shipping Address */}
            <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-6">
              <h3 className="text-md font-semibold text-white mb-3">
                Ship To
              </h3>
              <div className="text-sm text-slate-300">
                <p className="font-medium">{selectedOrder.customer.name}</p>
                <p>{selectedOrder.shippingAddress.line1}</p>
                {selectedOrder.shippingAddress.line2 && (
                  <p>{selectedOrder.shippingAddress.line2}</p>
                )}
                <p>
                  {selectedOrder.shippingAddress.city},{' '}
                  {selectedOrder.shippingAddress.state}{' '}
                  {selectedOrder.shippingAddress.postalCode}
                </p>
              </div>
            </div>
          </div>

          {/* Right Sidebar */}
          <div className="space-y-6">
            {/* Box Selection */}
            <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-6">
              <h3 className="text-md font-semibold text-white mb-4">
                Select Box
              </h3>

              {/* Order Volume Info */}
              <div className="mb-4 rounded-lg bg-slate-700/30 p-3">
                <div className="flex items-center justify-between text-sm">
                  <span className="text-slate-400">Order Volume:</span>
                  <span className="text-white">
                    {selectedOrder.totalVolume?.toLocaleString()} cu in
                  </span>
                </div>
                <div className="flex items-center justify-between text-sm mt-1">
                  <span className="text-slate-400">Order Weight:</span>
                  <span className="text-white">
                    {((selectedOrder.totalWeight || 0) / 16).toFixed(1)} lbs
                  </span>
                </div>
                {suggestedBox && (
                  <div className="flex items-center gap-2 mt-2 pt-2 border-t border-slate-600/30">
                    <Check className="h-4 w-4 text-emerald-400" />
                    <span className="text-xs text-emerald-400">
                      Suggested: {suggestedBox.name}
                    </span>
                  </div>
                )}
              </div>

              <div className="space-y-2">
                {shippingBoxes
                  .filter((b) => b.isActive)
                  .map((box) => {
                    const fitsVolume =
                      box.volume >= (selectedOrder.totalVolume || 0) * 1.1;
                    const fitsWeight =
                      box.maxWeight >= (selectedOrder.totalWeight || 0);
                    const fits = fitsVolume && fitsWeight;
                    const isSuggested = box.id === suggestedBox?.id;

                    return (
                      <button
                        key={box.id}
                        onClick={() => handleBoxSelect(box.id)}
                        className={`w-full flex items-center justify-between rounded-lg p-3 text-left transition-colors ${
                          selectedBoxId === box.id
                            ? 'bg-emerald-500/20 border border-emerald-500/50'
                            : fits
                            ? 'bg-slate-700/30 border border-slate-600/30 hover:bg-slate-700/50'
                            : 'bg-slate-700/20 border border-slate-700/30 opacity-50'
                        }`}
                        disabled={!fits}
                      >
                        <div>
                          <div className="flex items-center gap-2">
                            <p className="text-sm font-medium text-white">
                              {box.name}
                            </p>
                            {isSuggested && (
                              <span className="rounded bg-emerald-500/20 px-1.5 py-0.5 text-xs text-emerald-400">
                                Best Fit
                              </span>
                            )}
                          </div>
                          <p className="text-xs text-slate-400">
                            {box.dimensions.length}{box.dimensions.width}
                            {box.dimensions.height}&quot;  {box.volume} cu in
                          </p>
                        </div>
                        <div className="text-right">
                          <p className="text-sm text-slate-300">
                            {formatCurrency(box.cost)}
                          </p>
                          {!fits && (
                            <p className="text-xs text-red-400">Too small</p>
                          )}
                        </div>
                      </button>
                    );
                  })}
              </div>
            </div>

            {/* Shipping Rates */}
            <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-6">
              <h3 className="text-md font-semibold text-white mb-4">
                Shipping Rates
              </h3>
              <div className="space-y-2">
                {mockShippingRates.map((rate, index) => (
                  <button
                    key={index}
                    onClick={() => setSelectedRate(index)}
                    className={`w-full flex items-center justify-between rounded-lg p-3 text-left transition-colors ${
                      selectedRate === index
                        ? 'bg-purple-500/20 border border-purple-500/50'
                        : 'bg-slate-700/30 border border-slate-600/30 hover:bg-slate-700/50'
                    }`}
                  >
                    <div>
                      <p className="text-sm font-medium text-white">
                        {rate.carrier} {rate.service}
                      </p>
                      <p className="text-xs text-slate-400">
                        {rate.days} business days
                      </p>
                    </div>
                    <p className="text-sm font-medium text-white">
                      {formatCurrency(rate.price)}
                    </p>
                  </button>
                ))}
              </div>
            </div>

            {/* Actions */}
            <div className="space-y-3">
              <button
                onClick={handlePrintLabel}
                disabled={!selectedRate || !selectedBoxId}
                className="w-full flex items-center justify-center gap-2 rounded-lg bg-slate-700 py-3 text-sm font-medium text-white hover:bg-slate-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <Printer className="h-4 w-4" />
                Print Label
              </button>
              <button
                onClick={handleMarkShipped}
                disabled={!selectedRate || !selectedBoxId}
                className="w-full flex items-center justify-center gap-2 rounded-lg bg-emerald-500 py-3 text-sm font-medium text-white hover:bg-emerald-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <Truck className="h-4 w-4" />
                Mark Shipped
              </button>
            </div>
          </div>
        </div>
      )}

      {/* No Order Selected */}
      {!selectedOrder && (
        <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-12 text-center">
          <Package className="mx-auto h-16 w-16 text-slate-600" />
          <h3 className="mt-4 text-lg font-medium text-white">
            No Order Selected
          </h3>
          <p className="mt-2 text-sm text-slate-400">
            Scan a tote barcode or select an order to begin packing
          </p>

          {/* Quick Select */}
          <div className="mt-6">
            <p className="text-sm text-slate-400 mb-3">Orders ready to pack:</p>
            <div className="flex flex-wrap justify-center gap-2">
              {ordersToPack.slice(0, 5).map((order) => (
                <button
                  key={order.id}
                  onClick={() => {
                    setSelectedOrder(order);
                    if (order.suggestedBoxId) {
                      setSelectedBoxId(order.suggestedBoxId);
                    }
                    setSplitShipments([
                      {
                        id: `ship-new-1`,
                        orderId: order.id,
                        shipmentNumber: 1,
                        boxId: order.suggestedBoxId,
                        items: order.items.map((item) => ({
                          orderItemId: item.id,
                          productId: item.productId,
                          bundleId: item.bundleId,
                          quantity: item.quantity,
                        })),
                        status: 'packing',
                        gripperStickersUsed: order.gripperStickersNeeded,
                      },
                    ]);
                  }}
                  className="rounded-lg bg-slate-700/50 px-4 py-2 text-sm text-white hover:bg-slate-700 transition-colors"
                >
                  #{order.orderNumber}
                </button>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Split Shipment Modal */}
      {showSplitModal && selectedOrder && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm">
          <div className="w-full max-w-2xl rounded-xl bg-slate-800 border border-slate-700 p-6 shadow-xl">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-white">
                Split Shipment
              </h3>
              <button
                onClick={() => setShowSplitModal(false)}
                className="text-slate-400 hover:text-white"
              >
                <X className="h-5 w-5" />
              </button>
            </div>

            <p className="text-sm text-slate-400 mb-6">
              Drag items between shipments or use the buttons to move quantities.
            </p>

            <div className="grid grid-cols-2 gap-4">
              {splitShipments.map((shipment, shipmentIndex) => (
                <div
                  key={shipment.id}
                  className="rounded-lg bg-slate-700/30 border border-slate-600/30 p-4"
                >
                  <div className="flex items-center justify-between mb-3">
                    <h4 className="text-sm font-medium text-white">
                      Shipment {shipmentIndex + 1}
                    </h4>
                    <span className="text-xs text-slate-400">
                      {shipment.items.reduce((sum, i) => sum + i.quantity, 0)}{' '}
                      items
                    </span>
                  </div>

                  <div className="space-y-2">
                    {shipment.items.map((item) => {
                      const orderItem = selectedOrder.items.find(
                        (oi) => oi.id === item.orderItemId
                      );
                      return (
                        <div
                          key={item.orderItemId}
                          className="flex items-center justify-between rounded bg-slate-800/50 p-2"
                        >
                          <span className="text-xs text-white">
                            {orderItem?.name}
                          </span>
                          <div className="flex items-center gap-2">
                            <button
                              onClick={() => {
                                const otherIndex =
                                  shipmentIndex === 0 ? 1 : 0;
                                if (splitShipments[otherIndex]) {
                                  handleMoveItem(
                                    item.orderItemId,
                                    shipmentIndex,
                                    otherIndex,
                                    1
                                  );
                                }
                              }}
                              className="rounded bg-slate-700 p-1 text-slate-400 hover:text-white"
                            >
                              <Minus className="h-3 w-3" />
                            </button>
                            <span className="text-xs text-white w-6 text-center">
                              {item.quantity}
                            </span>
                            <button
                              onClick={() => {
                                const otherIndex =
                                  shipmentIndex === 0 ? 1 : 0;
                                const otherShipment =
                                  splitShipments[otherIndex];
                                const otherItem = otherShipment?.items.find(
                                  (i) => i.orderItemId === item.orderItemId
                                );
                                if (otherItem && otherItem.quantity > 0) {
                                  handleMoveItem(
                                    item.orderItemId,
                                    otherIndex,
                                    shipmentIndex,
                                    1
                                  );
                                }
                              }}
                              className="rounded bg-slate-700 p-1 text-slate-400 hover:text-white"
                            >
                              <Plus className="h-3 w-3" />
                            </button>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              ))}
            </div>

            {splitShipments.length < 3 && (
              <button
                onClick={() => {
                  handleAddShipment();
                }}
                className="mt-4 w-full rounded-lg border border-dashed border-slate-600 py-3 text-sm text-slate-400 hover:border-slate-500 hover:text-white transition-colors"
              >
                <Plus className="inline h-4 w-4 mr-1" />
                Add Shipment
              </button>
            )}

            <div className="flex justify-end gap-3 mt-6">
              <button
                onClick={() => setShowSplitModal(false)}
                className="rounded-lg bg-slate-700 px-4 py-2 text-sm font-medium text-white hover:bg-slate-600 transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={() => setShowSplitModal(false)}
                className="rounded-lg bg-emerald-500 px-4 py-2 text-sm font-medium text-white hover:bg-emerald-600 transition-colors"
              >
                Apply Split
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
